import Vue from "vue";
import Vuex from "vuex"
// 1.  vuex 
import moduleCart from './cart.js'
import moduleUser from './user.js'

Vue.use(Vuex);
const store = new Vuex.Store({
  modules:{
    // 2.  vuex  m_cart
    //     cart  m_cart/cart
    m_cart: moduleCart,
    // user
    m_user:moduleUser,
  }
})
export default store