import { mapGetters } from 'vuex'

//  mixin 
export default {
  computed: {
    ...mapGetters('m_cart', ['total']),
  },
  onShow() {
    // 
      this.setBadge()      
  },
  methods: {
    setBadge() {
      //  uni.setTabBarBadge() 
      if(this.total!==0){
        uni.setTabBarBadge({
          index: 2,
          text: this.total + '', // text 
        })
      }else{
        //  uni.removeTabBarBadge() 
        uni.removeTabBarBadge({
          index: 2,
        })
      }   
    },
  },
  // total
  watch:{
    total(){
          this.setBadge()       
    }
  }
}